@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    margin: 0;
    padding: 0;
}

.header__wrapper {
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 64px;
  padding: 0 50px;
}

.header__logo {
  margin: 0 48px;
}

.header__search {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  min-width: 620px;
  height: 39px;
  border: 1px solid #31363a;  
  border-radius: 4px;
  margin-right: auto;
}

.header__search__input {
  min-width: 100%;
  min-height: 100%;
  background: transparent;
  border: none;
  outline: none;
  padding-left: 5px;
}

.header__search > .MuiSvgIcon-root {
  padding: 20px;
}

.header__search > input::-webkit-input-placeholder {
  font-weight: 600;
  color: white;
}

.header__search > input:-ms-input-placeholder {
  font-weight: 600;
  color: white;
}

.header__search > input::placeholder {
  font-weight: 600;
  color: white;
}

.header__searchContainer > input:focus {
  outline: none;
}

.header__menuItems {
  margin-right: 48px;
}

.header__menuItems a {
  font-weight: 700;
  color: white;
  text-decoration: none;
  padding: 0 10px;
  white-space: nowrap;
}

.header__menuItems a:hover,
.header__menuItems a:active {
  color: #5ac53b;
}

a:first-of-type {
  color: #5ac53b;
}

.header__search_input {
  min-height: 100%;
}

* {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
}

body {
  background-color: #222222;
  padding-top: 10px;
}

.app {
  place-items: center;
}

.app__body {
  margin: 0 48px;
  padding: 0 50px;
}

.app__container {
  display: -webkit-flex;
  display: flex;
  padding-top: 36px;
  color: white;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

h1 {
  color: white;
}
.canvasjs-chart-toolbar {
  display: none;
}

canva,
canva *,
canva *::before,
canva *::after {
  position: static !important;
}

.newsfeed {
  width: 100%;
  z-index: 0;
}

.newsfeed__container > p {
  margin-bottom: 38px;
}

.newsfeed__chart {
  position: relative;
}

.err {
  display: block;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-63px, -50%);
          transform: translate(-63px, -50%);
  z-index: 999;
  font-weight: bold;
  left: 50%;
  margin: 0 auto;
}

.stats {
    border: 2px solid rgba(255,255,255,0.3);
    margin-left: 100px;
    height: 530px;
}   

.stat {
    padding: 25px 50px;
    border-bottom: 2px solid rgba(255,255,255,0.3);
    text-align: center;
    font-size: 12px;
    font-weight: bold;
    cursor: pointer;
}
html {
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}

*, *::after, *::before {
    box-sizing: inherit;
}

* {
    margin: 0;
    padding: 0;
}

li {
    list-style: none;
}

a {
    display: inline-block;
    text-decoration: none;
    color: blue;
}

input, button, textarea, select {
    color: inherit;
    font: inherit;
    letter-spacing: inherit;
}

input, textarea, button {
    border: 1px solid gray;
    outline: none; 
}

embed, iframe, img, object, video {
    display: block;
    max-width: 100%;
}


